// 导航栏事件
(function(){
    // 退出登录按钮事件
    $("#btn_logout").click(function(){
        $.loader(true);
        $.ajax({
            url: appConfig.adminPath + 'Site/logout',
            success: function(data){
                $.loader(false);
                if(data.success){
                    BootstrapDialog.show({
                        title: '提示',
                        message: '退出登录',
                        buttons: [{
                            label: '关闭',
                            action: function(dialogItself){
                                $.loader(true);
                                location.href = appConfig.adminPath + 'Site/login';
                            }
                        }],
                        onhide: function(dialogRef){
                            $.loader(true);
                            location.href = appConfig.adminPath + 'Site/login';
                        }
                    });
                }else{
                    BootstrapDialog.show({
                        message: data.errmsg,
                        buttons: [{
                            label: '关闭',
                            action: function(dialogItself){
                                dialogItself.close();
                            }
                        }]
                    });
                }
            }
        });
    });
})();


